
.Home {
  width: 100%;
  height: 100vh;
  position: relative;
}

.Navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 7.5em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #4169E1;
  border-bottom: solid 2px black;
  color: white;
}
#AddDoc {
  position: absolute;
  left: 1em;
  font-size: 3.2em;
}
#AddDoc:active {
  transition: transform 300ms linear;
  transform: scale(1.2);
}
.Logo {
  position: absolute;
  font-size: 2.2em;
  font-weight: 700;
}
.Login {
  position: absolute;
  right: 2em;
  font-size: 1.6em;
  font-weight: 600;
  text-decoration: none;
  color: white;
}
.Sair {
  position: absolute;
  right: 2em;
  font-size: 1.6em;
  font-weight: 600;
}
.CreatedModal {
  position: fixed;
  margin: auto;
  width: 50em;
  height: 40em;
  max-width: 80%;
  box-shadow: 0 0;
  border: solid 0.2em black;
  border-radius: 2em;
}
.CreatedModal::backdrop {
  background-color: rgba(0,0,0, 0.6);
}
.CreatedModal.open {
  animation: OpenModal 300ms ease;
}
.CreatedModal.close {
  animation: CloseModal 300ms ease;
}
#ExitModal {
  position: absolute;
  top: 0.6em;
  right: 0.6em;
  font-size: 2.4em;
  font-weight: 700;
}
.ModalTitulo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20%;
}
.ModalTitulo h1 {
  font-size: 1.8em;
}
.ModalForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5em;
  width: 100%;
  height: 60%;
}
.ModalForm label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 1.2em;
  font-weight: 600;
}
.ModalForm input {
  width: 250px;
  height: 36px;
  font-weight: 500;
  border: solid 2.5px black;
  border-radius: 0.5em;
  padding-left: 0.6em;
}
.ModalForm input.erro {
  border: solid 2.5px red;
  animation: erro 400ms linear;
}
.ModalForm textarea.erro {
   border: solid 2.5px red;
   animation: erro 400ms linear;
}
.ModalForm:focus, .ModalForm input {
  box-shadow: 0 0 0 0;
  outline: none;
}
.ModalForm textarea {
  width: 250px;
  height: 90px;
  font-size: 1em;
  font-weight: 600;
  font-family: 'Poppins', Sans-Serif;
  box-shadow: 0 0 0 0;
  outline: none;
  border: solid 2.5px black;
  border-radius: 0.5em;
  padding: 0.6em;
}

.CriarEvento {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20%;
}
.Push {
  background-color: #4169E1;
  width: 6em;
  height: 3em;
  border: none;
  border-radius: 0.4em;
}
.Push p {
  font-size: 1.2em;
  color: white;
  font-weight: 700;
}
.Push:active {
 transition: transform 300ms ease;
 transform: scale(1.2);
}

.Feed {
  margin-top: 8.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4em;
  width: 100%;
  min-height: 0vh;
  padding: 2em 0;
}
.Feed li {
  list-style: none;
  width: 58em;
  max-width: 90%;
  height: 24em;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #FFE978;
  border-radius: 2em;
  padding: 2em 2.6em;
  color: black;
}
.Titulo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30%;
}
.Titulo h1 {
  text-align: left;
  font-size: 1.5em;
}
.Texto {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 50%;
  padding-top: 0.6em;
}
.Texto p {
  font-size: 1.3em;
}
.Inf-extras {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  height: 20%;
}
.Inf-extras span{
  font-weight: 600;
  font-size: 1.2em;
}
.LoginModal {
  position: fixed;
  margin: auto;
  width: 30em;
  height: 30em;
  background-color: grey;
  padding: 2em;
  border-radius: 2em;
}

@keyframes OpenModal {
  0% {
     transform: translatex(-100%);
  }
  100% {
     transform: translatex(1);
  }
}

@keyframes CloseModal {
  0% {
    transform: translatex(1);
  }
  100% {
    transform: translatex(100%);
  }
}

@keyframes erro {
  0% {
    transform: translatex(0);
  }
  25% {
    transform: translatex(-0.6em);
  }
  50% {
    transform: translatex(1.2em);
  }
  75% {
    transform: translatex(-1.2em);
  }
  100% {
    transform: translatex(0);
  }
}